import { ReactNode } from 'react';
import styled from 'styled-components';

const Outter = styled.div`
    display: flex;
    justify-content: center;
`;

export default function ContentContainer({ children, min = '800px', max = '1000px' }: { children?: ReactNode; min?: string; max?: string }) {
    return (
        <Outter>
            <div style={{ minWidth: min, maxWidth: max }}>{children}</div>
        </Outter>
    );
}
