import ContentContainer from '../../../components/ContentContainer';

type CheckProps = { onChange: React.ChangeEventHandler<HTMLInputElement>; onClick: () => void };

export default function Check({ onChange, onClick }: CheckProps) {
    const attribute = {
        alt: 'aa'
    };

    return (
        <ContentContainer>
            <input placeholder='비번 입력' onChange={onChange} {...attribute}></input>
            <button onClick={onClick}>확인</button>
        </ContentContainer>
    );
}
