import React, { ReactNode } from 'react';
import styled from 'styled-components';

const Outter = styled.div`
    display: flex;
    flex-wrap: wrap;

    & > div {
        padding: 0.25rem;
        box-sizing: border-box;
    }
`;

export default function Grid({ children, columns = 2 }: { children: ReactNode; columns?: number | string }) {
    const cell = React.Children.map(children, el => <div style={{ width: `${100 / Number(columns)}%` }}>{el}</div>);

    return <Outter>{cell}</Outter>;
}
