import styled from 'styled-components';
import ContentContainer from '../../components/ContentContainer';
import { contentsList } from '../../assets/texts/contents';
import { useState } from 'react';

const Container = styled.div`
    display: flex;
    flex-wrap: wrap;
`;

const List = styled.div`
    padding: 1rem;
    width: 150px;
    height: calc(100vh - 2rem);
    overflow: scroll;
    & > div {
        cursor: pointer;
        padding: 1rem;
        border: 1px solid grey;
        text-align: center;
    }
`;
const Item = styled.div``;
const Content = styled.div`
    width: 100%;
    flex: 1;
    position: relative;
    & > iframe {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 1rem;
        left: 1rem;
        right: 1rem;
        bottom: 1rem;
    }
`;

export default function Contents() {
    const [frameId, setFrameId] = useState<null | string>(null);
    const [check, setCheck] = useState(false);

    if (!check) return <input onChange={e => e.target.value === '9398' && setCheck(true)} />;

    return (
        <ContentContainer min='1000px' max='1200px'>
            <Container>
                <List>
                    {contentsList.map(el => (
                        <Item key={el.frameId} onClick={() => setFrameId(el.frameId)}>
                            {el.title}
                        </Item>
                    ))}
                </List>
                <Content>{frameId && <iframe title='contents' src={`/history/dist/dist/?frame_id=${frameId}`} frameBorder='0'></iframe>}</Content>
            </Container>
        </ContentContainer>
    );
}
