import { useState } from 'react';
import styled from 'styled-components';

import { getIncome } from '../../utils/helper';
import { stockList } from '../../assets/data/stockList';

import useConfirmStock from './useConfirmStock';

import ContentContainer from '../../components/ContentContainer';
import Check from './Check';
import Table from './Table';

const list = stockList.map(el => {
    const { open: base, hight, low, open, close } = el;
    const income = getIncome({ base: Number(base), hight: Number(hight), limit: 2, low: Number(low), open: Number(open), close: Number(close) });
    return { ...el, income };
});

export default function Stock() {
    const { confirm, changeHandler, clickHandler } = useConfirmStock();
    if (!confirm) return <Check onChange={changeHandler} onClick={clickHandler} />;

    return (
        <ContentContainer>
            <Table></Table>
        </ContentContainer>
    );
}
