import React, { ReactNode, useState } from 'react';
import styled from 'styled-components';

type NavigatorProps = {
    value?: number;
    navis: ReactNode[];
    onClick?: (p: number) => void;
    children?: ReactNode;
};

const NaviOutter = styled.div`
    display: flex;
    & > div {
        cursor: pointer;
        flex: 1;

        &.focus {
            border: 1px solid red;
        }
        &.unfocus {
            border: 1px solid transparent;
        }
    }
`;

export default function Navigator({ value: initValue = 0, navis, onClick, children }: NavigatorProps) {
    const [value, setValue] = useState(initValue);
    const onClickHandler = (val: number) => {
        onClick && onClick(val);
        setValue(val);
    };

    return (
        <div>
            <NaviOutter>
                {navis.map((el, i) => (
                    <div key={`navi-${i}`} className={value === i ? 'focus' : 'unfocus'} onClick={() => onClickHandler(i)}>
                        {el}
                    </div>
                ))}
            </NaviOutter>
            <div>{React.Children.toArray(children)[value]}</div>
        </div>
    );
}
