import { useState } from 'react';
const useConfirmStock = () => {
    const [confirm, setConfirm] = useState(false);
    const [text, setText] = useState('');
    const changeHandler = (e: React.ChangeEvent<HTMLInputElement>) => setText(e.target.value);
    const clickHandler = () => text === '9398' && setConfirm(true);

    return { confirm, changeHandler, clickHandler };
};

export default useConfirmStock;
